@import "src/index.scss";






































































html, body, #app {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
}

#app {
    .app-head {
        height: 64px;
        background-color: #12b4a5;
        display: flex;
        align-items: center;
        justify-content: center;

        .app-head-content {
            width: 1000px;
            color: white;
            font-size: 27px;
            font-family: Arial;
        }
    }

    .app-desc {
        background-color: #f6f6f6;
        padding: 40px 0;
        letter-spacing: 1px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .app-desc-title {
            color: #333;
            font-size: 36px;
            margin-bottom: 20px;
        }

        .app-desc-text {
            color: #999;
            height: 44px;
            line-height: 44px;
        }
    }

    .app-toolbar {
        display: flex;
        justify-content: center;
        padding: 20px 0;
        user-select: none;

        .app-toolbar-item {
            cursor: pointer;
            padding: 9px 16px;
            text-decoration: none;
            color: #333;
            border-radius: 3px;

            &:not(:last-child) {
                margin-right: 16px;
            }

            &:hover {
                background-color: #f6f6f6;
            }

            &:active {
                background: #e2e2e2;
            }

            &.app-toolbar-item-active {
                background-color: #12b4a5;
                color: white;
            }
        }
    }

    .app-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 50px;

        .demo {
            .demo-title {
                font-size: 14px;
                font-family: Arial;
            }

            .demo-content {
                padding: 20px 0;
            }
        }
    }

    .custom-color {
        display: flex;
        justify-content: center;

        .pl-loading-tag {
            background-color: #FF7400;
        }
    }
}
